<template>
<div id="foot" class="container-fluid ">
  <div id="footer" >
        <div class="leftfoot">
                <p class="title"></p>
                <p class="title">AERSC@CUMT</p>
                <p class="address_tel_fax">
                  <span>1</span>
                  <span>221116</span>
                </p>
        </div>
        <div class="logo ">
          <img src="../assets/img/cumtlogo.png" class="img-responsive">
        </div>
        <div class="rightfoot">
            <img src="../assets/img/weixin.png" class="pic1 img-responsive">
            <img src="../assets/img/wx.png" class="pic img-responsive">
        </div>
  </div>
  <div>
      <p class="copy">Copyright &copy; 1909 - {{ new Date().getFullYear() }} </p>
  </div>  
</div>
</template>
<script>
export default {
  name: "Footer",
  data() {
    return {};
  }
};
</script>
<style scoped >
#foot {
  width: 100%;
  color: #fff;
  background:RGB(158, 158, 158);
  display: flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
}
#footer {
  width: 100%;
  color: #fff;
  background:RGB(158, 158, 158);
  display: flex;
  flex-direction:row;
  align-items:center;
  justify-content:space-around;
  gap: 15vw;
}
#footer .leftfoot{
  padding-top: 10vh;
  order: 1;
  align-self:flex-end;
  padding-left: 5vw;
}
#footer .logo {
  margin: 40px 20px;
  order: 2;
  aspect-ratio: 9/1;
}
#footer .rightfoot{
  order: 3;
  width: 20vw;
  cursor: pointer;	
  position: relative;
}
#footer .rightfoot img:nth-child(1){
  width:3vw;
}
#footer .rightfoot img:nth-child(2){
  width:6vw;
  position: absolute;
  top: -30px;
  display: none;
}
#footer .rightfoot:hover  img.pic{
  display: block;
  aspect-ratio: 1/1;
}
.title {
  font-size:clamp(1rem,1vw,2rem);
  margin-bottom: 0px;
}
.address_tel_fax {
  color: #d3d3d3;
  font-size: clamp(1rem,1vw,2rem);
  margin: 10px 0;
  display: flex;
  flex-direction:column;
}
.email_wx {
  color: #d3d3d3;
  font-size: 14px;
}
.copy {
  color: #d3d3d3;
  font-size: 1.5vh;

}

@media screen and (max-width: 997px) {
  .title {
    font-size: 20px;
  }
  .address_tel_fax {
    font-size: 12px;
  }
  .email_wx {

  font-size: 12px;
}
.copy {
  font-size: 12px;
  margin: 30px 0 10px;
}
}

</style>




