<template>
  <!--  -->
  <div id="header" class="container-fuild">
    <!--  -->
    <div class="header-top">
          <span class="tittle">Atmospheric Environmental<span class="tittle">Remote Sensing<span class="tittle">Cooperative</span></span></span>
        <!-- <div class="shejiao pull-right">
          <span class="glyphicon glyphicon-hand-right"></span>
          <span class="glyphicon glyphicon-hand-left"></span>
        </div> -->
    </div>
    <!--  -->
    <div class="header-nav  hidden-xs-only" >
      <div>
          <p class="tittle"></p>
      </div>
      <!--  -->
      <ul class="header-nav-wrapper hidden-xs-only" >
        <li
          v-for="(item,index) in navList"
          :key="index"
          :class="index==navIndex?'active':''"
          @click="navClick(index,item.name)"
        >
          <router-link :to="item.path">
            {{item.name}}
            <span v-if="item.children.length>0" class="glyphicon glyphicon-menu-down"></span>
            <i class="underline"></i>
          </router-link>
          <dl v-if="item.children.length>0">
            <dt v-for="(i,n) in item.children" :key="n">
              <router-link :to="i.path">{{i.name}}</router-link>
            </dt>
          </dl>
        </li>
      </ul>
    </div>
    <!--  -->
    <div class="header-nav-m container-fuild visible-xs">
       <div class="header-nav-m-logo">
        <img class="center-block" src="@/assets/img/0.png" alt="logo">
      </div>
      <!--  -->
      <div class="header-nav-m-menu text-center moblie" >
        {{menuName}}
        <div
          class="header-nav-m-menu-wrapper"
          data-toggle="collapse"
          data-target="#menu"
          @click="menuClick"
        >
        <span :class="menuClass"></span>
        </div>
        
        <!--  -->
        <ul id="menu" class="header-nav-m-wrapper collapse">
          <li
            v-for="(item,index) in navList"
            :key="index"
            :class="index==navIndex?'active':''"
            @click="navClick(index,item.name)"
            data-toggle="collapse"
            data-target="#menu"
          >
            <router-link :to="item.path">
              {{item.name}}
              <i class="underline"></i>
            </router-link>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import 'element-ui/lib/theme-chalk/display.css';
export default {
  name: "Header",
  data() {
    return {
      navIndex: sessionStorage.getItem('navIndex') ? sessionStorage.getItem('navIndex') : 0,
      menuName: "",
      menuClass: "glyphicon glyphicon-menu-down",
      navList: [
        {
          name: "HOME",
          path: "/home",
          children: []
        },
        {
          name: "PUBLICATION",
          path: "/publiaction",
          children: []
        },
        {
          name: "NEWS",
          path: "/newsinformation",
          children: []
        },
        {
          name: "PEOPLE",
          path: "/people",
          children: []
        },
        {
          name: "DATA",
          path: "/jobchance",
          children: []
        },
        {
          name: "PROJECT",
          path: "/Project",
          children: []
        },
        {
          name: "JOIN US",
          path: "/contactus",
          children: []
        }
      ]
    };
  },
  watch:{
    '$route'(to,from){
      // if(to.name != this.menuName)
      // this.navClick(to.query.index, to.name)
      for(let i = 0; i < this.navList.length; i++) {
          if( this.navList[i].path == to.matched[1].path) {
            this.navClick(i,this.navList[i].name)
          }
        }
       
    },
  },
  mounted(){

  },
  methods: {
    navClick(index, name) {
      this.navIndex = index;
      sessionStorage.setItem('navIndex',index)
      this.menuName = name;
    },
    menuClick() {
      if (this.menuClass == "glyphicon glyphicon-menu-down") {
        this.menuClass = "glyphicon glyphicon-menu-up";
      } else {
        this.menuClass = "glyphicon glyphicon-menu-down";
      }
    },
  }
};
</script>
<style scoped>
/*  */
@-webkit-keyframes colorchange {
      0% {
        
        color: #22923a;
      }
      
      10% {
        
        color: hsl(67, 76%, 21%);
      }
      
      20% {
        
        color: rgb(26, 119, 57);
      }
      30% {
        
        color: rgb(160, 196, 103);
      }  
      50% {
        
        color: rgb(60, 94, 50);
      }
      
      60% {
        
        color: #83e99d;
      }
      
      70% {
        
        color: hsl(78, 86%, 44%);
      }
      80% {
     
        color: hsl(44, 86%, 54%);
      }
      
      90% {
     
        color: hsl(34, 77%, 48%);
      }
      
      100% {
        
        color: rgb(173, 100, 5);
      }
    }
/*  */
#header {
  background: #ffffff;
  transition: all ease 0.6s;
  display: flex;
  flex-direction:column;
  justify-content: space-between;
  align-items:center;
}
#header .header-top {
  color: hsl(120, 100%, 1%);
  background: #ffffff;
  display: flex;
  flex-direction:row;
  justify-content:space-between;
  width: 80vw;
  border-bottom: 4px solid #a09d9d;
  line-height: 8vh;
  border-image: linear-gradient(to right, #21b057, #335495) 1
}

#header .tittle{
  font-size: 2vw;
  font-weight: 900;
  
}
.header-top .tittle{
  color: #21b057;
}
.header-top .tittle span span {
  color:#335495
}
.header-top .tittle span  {
  color:#000000
}
/*  */
#header .header-top span {
  margin: 0 8px;
}
#header .header-nav p{
  margin: 0 8px;
}
/*  */
#header .header-nav {
  width: 80vw;
  padding-right: 0;
  padding-left:0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items:center;
}

/* logo */
#header .header-nav .header-nav-logo {
  width: 100px;
  height: 100%;
  float: left;
  position: relative;
}
/* logo */
#header .header-nav .header-nav-logo img {
  width: 95px;
  height: 45px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}
/*   */
#header .header-nav-fixed .header-nav-wrapper {
  line-height:8vh;
}
#header .header-nav .header-nav-wrapper {
  line-height: 8vh;
  margin: 0;
}
/*   */
#header .header-nav .header-nav-wrapper > li {
  float: left;
  margin: 0 15px;
  position: relative;
  font-size: clamp(1rem,1vw,2rem);;
}
/*   a  */
#header .header-nav .header-nav-wrapper > li > a {
  color: #000;
  font-size:clamp(1rem,1vw,2rem);;
  font-weight: bold;
  padding: 15px 0;
  position: relative;
}
/*   a  */
#header .header-nav .header-nav-wrapper > li > a > i {
  display: block;
  position: absolute;
  bottom: -2px;
  left: 50%;
  width: 0;
  height: 2px;
  opacity: 0;
  transition: all 0.6s ease;
  background-color: #1e73be;
}
/*   a  */
#header .header-nav .header-nav-wrapper > li > a > span {
  font-size: 1.5vh;
  transition: transform ease 0.5s;
}
/*   a   */
#header .header-nav .header-nav-wrapper > li > a:hover {
  color: #1e73be;
  text-decoration: none;
}
/*   a   */
#header .header-nav .header-nav-wrapper > li > a:hover .underline {
  opacity: 1;
  width: 100%;
  left: 0;
}
/*   a   */
#header .header-nav .header-nav-wrapper > li > a:hover span {
  transform: rotate(180deg);
}
/*   a   */
#header .header-nav .header-nav-wrapper > li.active > a {
  color: #1e73be;
  text-decoration: none;
  border-bottom: 2px solid #1e73be;
}
/*   */
#header .header-nav .header-nav-wrapper > li > dl {
  display: none;
  position: absolute;
  width: 168px;
  top: 80%;
  left: 0;
  z-index: 999999;
  box-shadow: 0 0 3px 1px #ccc;
  background: #fff;
}
/*   */
#header .header-nav .header-nav-wrapper > li > dl > dt {
  width: 100%;
  padding: 10px;
  border-bottom: 1px solid #ccc;
}
/*   */
#header .header-nav .header-nav-wrapper > li > dl > dt > a:hover {
  text-decoration: none;
}
/*   */
#header .header-nav .header-nav-wrapper > li:hover dl {
  display: block;
}
#header .header-nav .header-nav-wrapper > li > dl > dt:hover {
  cursor: pointer;
  background: #ccc;
}
/* */
.moblie{
  margin-left:70vw;
}
@media screen and (max-width: 997px) {
  #header .header-top{
    width: 100%;
  }
  #header .header-nav-m {
    width: 100vw;
    padding-right: 0;
    padding-left:0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items:center;
    font-size:clamp(1rem,1vw,2rem);
  }
  /* logo */
  #header .header-nav-m .header-nav-m-logo {
    width: 100%;
    display: inline;
    position:absolute;
  
  }
  /* logo */
  #header .header-nav-m .header-nav-m-logo img {
    width: 50px;
    height: 50px;
    margin-left: 20px ;
  }
  /*    */
  #header .header-nav-m .header-nav-m-menu {
    color: #fff;
    line-height: 6vh;
    background: #b4b2b2;
    position: relative; 
    
  }
  #header .header-nav-m .header-nav-m-menu span{
    position: relative; 
    margin-top: 2rem;
    margin-left: 6rem;
  }
  /*   */
  #header .header-nav-m .header-nav-m-menu-wrapper {
    position: absolute;
    top: 50%;
    right: 20px;
    margin-top: -20px;
    width: 50px;
    height: 40px;
    color: #252424;
    z-index: 999999;
    font-size:clamp(1rem,1vw,2rem);
  }
  /*  */
  #header .header-nav-m .header-nav-m-wrapper {
    position: absolute;
    top: 2.5vh;
    left: 0;
    width: 100%;
    background: #474747;
    z-index: 9999999;
  }
  /*   */
  #header .header-nav-m .header-nav-m-wrapper > li {
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #ccc;
    position: relative;
  }
  /*   a  */
  #header .header-nav-m .header-nav-m-wrapper > li > a {
    color: #fff;
    font-size:clamp(1rem,1vw,2rem);
    font-weight: bold;
    padding: 15px 0;
    position: relative;
  }
  /*   a  */
  #header .header-nav .header-nav-wrapper > li > a > span {
    font-size: 10px;
  }
}
</style>



