import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      redirect: '/'
    }, {
      path: '/',
      name: 'PageView',
      component: resolve => require(['@/view/PageView'], resolve),
      children: [
        {
          path: '/',
          redirect: '/home',
        }, {
          path: '/home',
          name: 'home',
          component: resolve => require(['@/view/HomePage'], resolve),
          meta: {
            title: ''
          }
        }, {
          path: '/Project',
          name: 'Project',
          component: resolve => require(['@/view/Project'], resolve),
          meta: {
            title: 'Project'
          },
        }, {
          path: '/publiaction',
          name: 'publiaction',
          redirect:'/publicationbox',
          component: resolve => require(['@/view/Tpublication'], resolve),
          meta: {
            title: 'PUBLICATION'
          },
          children:[
            {
              path: '/publiaction/publicationDetail/:number',
              name: 'publiactionDetail',
              component: resolve => require(['@/view/publicationDetail'], resolve),
              meta: {
                title: 'PUBLICATION DETAIL'
              }
            },
          ]
        }, 
        {
          path: '/publicationbox',
          name: 'publicationbox',
          component: resolve => require(['@/view/publication'], resolve),
          meta: {
            title: 'PUBLICATION'
          }
        },
        {
          path: '/newsinformation',
          name: 'newsinformation',
          redirect:'/newsinformation/news',
          component: resolve => require(['@/view/newsView'], resolve),
          meta: {
            title: 'NEWS'
          },
          children:[
            {
              path: '/newsinformation/details:id',
              name: 'newsinformation details',
              component: resolve => require(['@/view/NewsDetail'], resolve),
              meta: {
                title: 'NEWS DETAILS'
              }
            },
          ]
        }, 
        {
          path: '/newsinformation/news',
          name: 'newsinformation',
          component: resolve => require(['@/view/NewsInformation'], resolve),
          meta: {
            title: 'NEWS'
          }
        },
        {
          path: '/people',
          name: 'people',
          redirect:'/peoplebox',
          component: resolve => require(['@/view/peopleView'], resolve),
          meta: {
            title: 'PEOPLE'
          },
          children:[
           {
            path: '/people/peopleDetail/:name',
            name: 'peopleDetail',
            component: resolve => require(['@/view/peopleDetail'], resolve),
            meta: {
              title: 'PEOPLE DETAIL'
            },
            }
          ]
        }, 
        {
          path: '/peoplebox',
          name: 'peoplebox',
          component: resolve => require(['@/view/people'], resolve),
          meta: {
            title: 'PEOPLE'
          }
        },{
          path: '/jobchance',
          name: 'jobchance',
          component: resolve => require(['@/view/JobChance'], resolve),
          meta: {
            title: 'DATA'
          }
        }, {
          path: '/contactus',
          name: 'contactus',
          component: resolve => require(['@/view/ContactUs'], resolve),
          meta: {
            title: 'JOIN US'
          }
        },
      ]
    }
  ]
})



