<template>
  <div class="container">
      <p style="color: #457bc7;">YOU ARE HERE :</p>
      <el-breadcrumb v-for="(item,index) in breadList" :key="index">
        <el-breadcrumb-item :to="item.path">{{ item.meta.title }}</el-breadcrumb-item>
        <p></p>
      </el-breadcrumb>
  </div>
</template>

<script>
export default {
  name: "Bread",
  data() {
    return {
      breadList: [] // 
    };
  },
  watch: {
    $route() {
      this.getBreadcrumb();
    }
  },
  methods: {
    isHome(route) {
      return route.name === "home";
    },
    getBreadcrumb() {
      let matched = this.$route.matched;
      //
      if (!this.isHome(matched[1])) {
        matched = [{ path: "/home", meta: { title: "HOME" } }].concat(matched);
      }
      this.breadList = matched.filter(item=> item.name != "PageView");
    }
  },
  created() {
    this.getBreadcrumb();
  }
};
</script>
<style scoped>
.container{
  display: flex;
  flex-direction: row;
  justify-content:flex-start;
  align-items: center;
  gap: 1vw;
  margin-top: 2vh;
  border-bottom: 2px solid#c9c8c6;
}
.container p{
  margin-top: auto;
  margin-bottom: auto;
  font-weight: 600;
  font-size:  clamp(1rem,1vw,2rem);
}
.container div{
  display: flex;
  flex-direction: row;
  justify-content:flex-start;
  align-items: center;
  font-size: clamp(1rem,1vw,2rem);
}
</style>


